<template>
  <div id="app">
    <div class="content">
      <el-row type="flex" align="middle" class="row">
        <el-col :span="4">
          <router-link to="/PC/">
            <div>
              <img src="/static/logo.png" width="80px" height="80px" alt="" />
            </div>
          </router-link>
        </el-col>
        <el-col :span="20">
          <!-- 搜索框第一行 -->
          <el-row>
            <el-col :span="12" :push="1">
              <el-row>
                <el-col>
                  <div style="margin-top: 4px" @keyup.enter="search()">
                    <el-autocomplete
                      v-model="searchData"
                      :fetch-suggestions="querySearch"
                      placeholder="请输入关键词搜索"
                      :trigger-on-focus="true"
                      :clearable="true"
                      @select="handleSelect"
                      style="display: block"
                    >
                      <el-button slot="append" icon="el-icon-search" @click="search()"></el-button>
                    </el-autocomplete>
                  </div>
                </el-col>
              </el-row>
            </el-col>
            <!-- 购物车 -->
            <el-col :span="4" :push="2" style="margin-top: 4px">
              <router-link to="/PC/cart">
                <el-badge :value="getCartsLength" :max="99" class="item">
                  <el-button icon="el-icon-shopping-cart-full" size="medium">
                    我的购物车</el-button>
                </el-badge>
              </router-link>
            </el-col>
          </el-row>
        </el-col>
      </el-row>
    </div>
  </div>
</template>
 
<script>
export default {
  name: 'Search',
  data() {
    return {
      searchData: '',
      restaurants: [],
    };
  },
  computed: {
    getCartsLength() {
      if (this.$store.state.userInfo === null) {
        return 0;
      } else {
        console.log (this.$store.state.userInfo);
        return this.$store.state.userInfo.carts.length || 0;
      }
    },
  },
  methods: {
    querySearch(queryString, cb) {
      let restaurants = this.restaurants;
      let results = queryString
        ? restaurants.filter(this.createFilter(queryString))
        : restaurants;
      // 调用 callback 返回建议列表的数据
      cb(results);
    },
    createFilter(queryString) {
      return (restaurant) => {
        // 根据关键词生成正则表达式
        let patt = new RegExp(queryString.toLowerCase());
        // console.log (patt);
        // console.log (queryString.toLowerCase());
        // 过滤返回出符合结果的数据
        return patt.test(restaurant.value.toLowerCase());
      };
    },
    loadAll() {
      return [
        { value: '小米' },
        { value: '苹果' },
        { value: '三星' },
        { value: 'realme' },
        { value: 'apple' },
        { value: '手机 | 小米手机' },
        { value: '手机 | 三星手机' },
        { value: '手机 | 苹果手机' },
        { value: '手机 | 华为手机' },
      ];
    },
    handleSelect(item) {
      console.log(item);
    },
    search() {
      this.$router.push(`/PC/search?q=${this.searchData}&page=1&size=10`);
    },
  },
  mounted() {
    this.restaurants = this.loadAll();
  },
};
</script>

<style scoped>
#app {
  background-color: #fff;
  height: 100px;
  box-shadow: 0 4px 4px #ccc;
}

.content {
  width: 1200px;
  height: 100%;
  margin: 0 auto;
}

.row {
  height: 100%;
}

.searchList {
  position: relative;
  top: 38px;
}

.searchList .el-link {
  font-size: 16px;
}
</style>


